.search {
  margin-top: -30px;
  padding: 0;
  height: 200px;

  background-image: url("../images/pattern-bg-desktop.png");
}
.search > input {
  height: 30px;
  width: 40%;
  border-radius: 5px 0 0 5px;
  border: none;
  left: 15%;
  right: 15%;
}
.search > h3 {
  padding-top: 20px;
  color: white;
}
.search > button {
  height: 32px;
  background-color: black;
  color: white;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
}
/*.container {
  height: 120px;
  width: 800px;
}*/

.container {
  height: 40%;
  width: 60%;
}
.details {
  width: 60%;
  display: flex;
  flex-wrap: wrap;
  column-gap: 50px;
  width: fit-content;
  background-color: white;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  box-shadow: rgba(0, 0, 0, 0.9) 0px 5px 15px;
  color: rgb(166, 164, 164);
  padding: 10px;
  font-size: small;
  text-align: left;
  font-weight: 400;
  position: absolute;
  top: 15%;
  left: 15%;
  right: 15%;
}
.value {
  font-weight: 700;
  color: black;
  font-size: 18px;
}

.marker {
  background-image: url("../images/icon-location.svg");
  position: absolute;
  height: 100px;
  width: 100px;
  background-repeat: no-repeat;
}
@media only screen and (max-width: 768px) {
  .details {
    display: block;

    width: 200px;
    height: 200px;
    background-color: white;
    border-radius: 5px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    box-shadow: rgba(0, 0, 0, 0.9) 0px 5px 15px;
    color: rgb(166, 164, 164);
    padding: 10px;
    font-size: 8px;
    text-align: center;
    font-weight: 500;
    position: absolute;
    top: 15%;
    left: 15%;
    right: 15%;
  }

  .value {
    font-weight: 700;
    color: black;
    font-size: 11px;
  }
}
